.container{margin:auto;max-width:800px;margin-top:140px}.container .block-process{border-top-left-radius:15px;border-top-right-radius:15px;font-size:1.3rem;background-color:#aff5d1;display:flex;align-items:center;padding:16px;justify-content:space-around}.container .block-process:first-child{color:#333}.container .block-process .process-item{position:relative;font-weight:500;width:24%;display:flex;flex-direction:column;align-items:center}.container .block-process .process-item::after{border-top:2.5px dashed #777;content:"";height:1px;left:77%;position:absolute;top:40%;width:42%}.container .block-process .process-item:first-child::after{border-color:var(--primary-text)}.container .block-process .process-item:nth-child(2)::after{border-color:var(--primary-text)}.container .block-process .process-item:nth-child(2)::after{border-color:var(--primary-text)}.container .block-process .process-item:nth-child(3)::after{border-color:var(--primary-text)}.container .block-process .process-item:last-child::after{border-top:none}.container .block-process .process-item .icon{display:flex;align-items:center;justify-content:center;border-radius:50%;height:35px;width:35px;margin:auto;border:solid 1px #777}.container .block-process .process-item.active{color:var(--primary-text)}.container .block-process .process-item.active .icon{border-color:var(--primary-text)}.container .block-box{border-bottom-left-radius:15px;border-bottom-right-radius:15px;box-shadow:0 1px 2px 0 rgba(60,64,67,.1),0 2px 6px 2px rgba(60,64,67,.15);padding:10px}.container .block-box .text-thank{margin:0 16px 16px 16px;line-height:2.9rem}.container .block-box .text-thank .text-note{font-style:italic}.container .block-box .block-box-infor{color:#155724;background:#d4edda;border-radius:15px;box-shadow:0 1px 2px 0 rgba(60,64,67,.1),0 2px 6px 2px rgba(60,64,67,.15);padding:10px}.container .block-box .block-box-infor h4{font-size:2.4rem;margin-bottom:8px;text-align:center}.container .block-box .block-box-infor .infor{font-weight:600;margin:0 0 0 10px;text-transform:capitalize;font-weight:600;font-size:1.7rem;margin:15px 0}.container .block-box .block-box-infor .infor span{margin-left:10px;font-weight:400}.container .block-box .block-payment h4{margin:16px 8px}.container .block-box .block-payment .payment-group{display:flex;flex-wrap:wrap;justify-content:space-between}.container .block-box .block-payment .payment-group .payment-item{border-radius:15px;box-shadow:0 1px 2px 0 rgba(60,64,67,.1),0 2px 6px 2px rgba(60,64,67,.15);height:75px;margin-bottom:10px;width:calc(50% - 5px);display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:rgba(0,0,0,0);position:relative;padding:25px 15px;overflow:hidden;border:1.25px solid rgba(0,0,0,0)}.container .block-box .block-payment .payment-group .payment-item span{margin-top:4px;display:block;text-align:center;font-size:12px;font-weight:600}.container .block-box .block-payment .payment-group .payment-item img{background-color:rgba(0,0,0,0);width:65%;height:auto}.container .block-box .block-payment .payment-group .payment-item:hover{border:1.25px solid var(--primary-text)}.container .block-box .block-payment .payment-group .payment-item:hover::after{background:var(--primary-text);border-radius:0 0 0 .5rem;color:#fff;content:"✓";font-size:10px;height:13px;padding-left:5px;position:absolute;right:0;top:0;width:18px}.container .block-box .product-list{display:flex;flex-wrap:wrap;margin:24px 0;border-radius:15px;box-shadow:0 1px 2px 0 rgba(60,64,67,.1),0 2px 6px 2px rgba(60,64,67,.15);padding:10px}.container .block-box .product-list .product-item{width:calc(50% - 5px);border:1px solid #eee;border-radius:15px;display:flex;justify-content:space-between;padding:5px;font-size:1.6rem;margin-bottom:6px}.container .block-box .product-list .product-item img{width:calc(25% - 5px);height:auto}.container .block-box .product-list .product-item .item-infor{flex:1}.container .block-box .product-list .product-item .item-infor .item-name{font-weight:500;margin:5px 0}.container .block-box .product-list .product-item .item-infor .item-name span{font-weight:400;color:#777;font-style:italic}.container .block-box .product-list .product-item .item-infor .item-price{font-weight:500;margin:5px 0}.container .block-box .product-list .product-item .item-infor .item-price span{font-weight:400}.container .block-box .product-list .product-item .item-infor .item-qty{font-weight:500;margin:5px 0}.container .block-box .product-list .product-item .item-infor .item-qty span{font-weight:400}.btn-submit{margin-top:12px}.btn-submit button{margin-bottom:12px}@media only screen and (max-width: 768px){.container{margin-top:75px;padding:12px}.container .block-process{display:flex;padding:10px 0;font-size:1.2rem}.container .block-process .process-item p{font-size:1.2rem;text-align:center}.container .block-box .product-list{display:flex;flex-direction:column}.container .block-box .product-list .product-item{width:100%}.container .block-box .product-list .product-item img{width:40%}}